<template>
  <v-card-text>
    <v-container>
      <v-row>
        <v-col cols="12">
          <v-text-field
            v-model="user.name"
            :label="$t('forms.labels.name')"
            outlined
            dense
            :rules="[rules.required]"
          ></v-text-field>
          <v-select
            v-model="user.gender"
            :items="['male', 'female', 'other']"
            :label="$t('forms.labels.gender')"
            :rules="[rules.required]"
            outlined
            dense
          ></v-select>
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="date"
                :label="$t('forms.labels.dateOfBirth')"
                outlined
                dense
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker
              ref="picker"
              v-model="date"
              :max="new Date().toISOString().substr(0, 10)"
              min="1950-01-01"
              @change="save"
            ></v-date-picker>
          </v-menu>
          <v-textarea
            v-model="user.bio"
            :label="$t('forms.labels.bio')"
            outlined
            dense
          ></v-textarea>
        </v-col>
      </v-row>
    </v-container>
  </v-card-text>
</template>

<script>
import moment from 'moment'
import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  props: {
    user: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      date: moment(this.user.dob).format('YYYY-MM-DD'),
      menu: false
    }
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
    },
    user(to, from) {
      this.date = moment(to.dob).format('YYYY-MM-DD')
    },
    date(to, from) {
      this.user.dob = to
    }
  },
  methods: {
    save(date) {
      this.$refs.menu.save(date)
    }
  }
}
</script>

<style></style>
